.textInput {
  height: 64px;
  border-radius: 8px;
  box-shadow: none;
  border: 1px solid #e7ebee;
  font-size: 24px;
  text-align: center;
  width: 100%;
}

.textInput:focus,
.textInput:active {
  outline: none;
  border: 1px solid #a7abae;
}

.textInput-wrapper {
  margin: 0 8px;
  width: 100%;
}
