body {
  margin: 0;
  padding: 0;
  font-family: 'Graphik', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'Graphik-BoldItalic';
  margin-bottom: 0;
}

p {
  line-height: 1.5;
  margin: 16px 0;
  color: #8899a9;
}

.textFields {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  max-width: 600px;
  margin: 16px 0;
  width: 100%;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  text-align: center;
  background: url(/backdrop/static/media/background.1fba0f4b.svg) center no-repeat;
  background-size: cover;
  padding: 16px;
}

.backgroundGif {
  position: absolute;
  width: 80vw;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
  opacity: 0.2;
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 32px;
  border-radius: 8px;
  background: white;
  min-width: 640px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

.btn {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Graphik-Medium';
  height: 56px;
  padding: 0 24px;
  background-color: #3acc8a;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: medium;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.btn:hover {
  background-color: #2abc7a;
}

.btn:active,
.btn:focus {
  outline: none;
}

.textInput {
  height: 64px;
  border-radius: 8px;
  box-shadow: none;
  border: 1px solid #e7ebee;
  font-size: 24px;
  text-align: center;
  width: 100%;
}

.textInput:focus,
.textInput:active {
  outline: none;
  border: 1px solid #a7abae;
}

.textInput-wrapper {
  margin: 0 8px;
  width: 100%;
}

